import Tippy from "@tippyjs/react/headless";
import SettingMenu from "./components/SetTingMenu";
import DarkModeToggle from "react-dark-mode-toggle";
import useDarkMode from "./hooks/use-dark-mode";

import styles from "./App.module.scss";
import Header from "./components/Header";
import HanziComponent from "./components/HanziComponent";
import FormData from "./components/FormData";
import Input from "./components/SetTingMenu/Input";
import { useState } from "react";
import createPersistedState from "./hooks/use-persisted-state";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGear } from "@fortawesome/free-solid-svg-icons";
function App() {
	const [data, setData] = useState([]);
	const [isReading, setIsReading] = useState(false);
	const theme = useDarkMode();

	const [hanziComponentOptions, setHanziComponentOptions] =
		createPersistedState("hanziComponentOptions")({
			showOutline: true,
			width: 150,
			height: 150,
			padding: 20,
			strokeAnimationSpeed: 1,
			delayBetweenStrokes: 1000,
			strokeColor: "#555",
			radicalColor: null,
			highlightColor: "#AAF",
			outlineColor: "#DDD",
			drawingColor: "#333",
			drawingWidth: 4,
			highlightOnComplete: true,
			// highlightCompleteColor: null,
			// set renderer prop to 'canvas' to render using a 2d canvas instead of SVG.
			// May have better performance on some devices.
			//👉 dung voi canvas se gay bug o clean-up trong hanzi component.
			renderer: "svg",
		});

	const handleChangeSetting = (event) => {
		let val;
		if (event.target.type === "checkbox") {
			val = event.target.checked;
		} else if (event.target.type === "number") {
			val = Number(event.target.value);
			if (val < event.target.min) val = +event.target.min;
			if (val > event.target.max) val = +event.target.max;
		} else {
			val = event.target.value;
		}
		setHanziComponentOptions((preState) => {
			return { ...preState, [event.target.name]: val };
		});
	};

	const dataSettings = {
		data: {
			lable: "customs",
			listItems: [
				{
					lable: "theme",
					item: (
						<DarkModeToggle
							onChange={theme.toggle}
							checked={theme.value}
							size={45}
						/>
					),
				},
				{
					lable: "highlightOnComplete",
					item: (
						<div>
							{" "}
							<Input
								name='highlightOnComplete'
								type='checkbox'
								checked={
									hanziComponentOptions.highlightOnComplete
								}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
						</div>
					),
				},
				{
					lable: "showOutline",
					item: (
						<div>
							{" "}
							<Input
								name='showOutline'
								type='checkbox'
								checked={hanziComponentOptions.showOutline}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
						</div>
					),
				},
				{
					lable: "width",
					item: (
						<div>
							{" "}
							<Input
								name='width'
								type='number'
								min={100}
								max={500}
								defaultValue={hanziComponentOptions.width}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
							<span style={{ marginLeft: 5 }}>px</span>
						</div>
					),
				},
				{
					lable: "height",
					item: (
						<div>
							{" "}
							<Input
								name='height'
								type='number'
								min={100}
								max={500}
								defaultValue={hanziComponentOptions.height}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
							<span style={{ marginLeft: 5 }}>px</span>
						</div>
					),
				},
				{
					lable: "padding",
					item: (
						<div>
							{" "}
							<Input
								name='padding'
								type='number'
								min={10}
								max={50}
								defaultValue={hanziComponentOptions.padding}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
							<span style={{ marginLeft: 5 }}>px</span>
						</div>
					),
				},

				{
					lable: "strokeAnimationSpeed",
					item: (
						<div>
							{" "}
							<Input
								name='strokeAnimationSpeed'
								type='number'
								min={0.25}
								max={2}
								defaultValue={
									hanziComponentOptions.strokeAnimationSpeed
								}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
							<span style={{ marginLeft: 5 }}>step</span>
						</div>
					),
				},
				{
					lable: "delayBetweenStrokes",
					item: (
						<div>
							{" "}
							<Input
								name='delayBetweenStrokes'
								type='number'
								min={500}
								max={5000}
								defaultValue={
									hanziComponentOptions.delayBetweenStrokes
								}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
							<span style={{ marginLeft: 5 }}>ms</span>
						</div>
					),
				},
				{
					lable: "outlineColor",
					item: (
						<div>
							{" "}
							<Input
								name='outlineColor'
								type='color'
								value={hanziComponentOptions.outlineColor}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
						</div>
					),
				},
				{
					lable: "strokeColor",
					item: (
						<div>
							{" "}
							<Input
								name='strokeColor'
								type='color'
								value={hanziComponentOptions.strokeColor}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
						</div>
					),
				},
				{
					lable: "radicalColor",
					item: (
						<div>
							{" "}
							<Input
								name='radicalColor'
								type='color'
								value={hanziComponentOptions.radicalColor}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
						</div>
					),
				},
				{
					lable: "drawingColor",
					item: (
						<div>
							{" "}
							<Input
								name='drawingColor'
								type='color'
								value={hanziComponentOptions.drawingColor}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
						</div>
					),
				},
				{
					lable: "highlightColor",
					item: (
						<div>
							{" "}
							<Input
								name='highlightColor'
								type='color'
								value={hanziComponentOptions.highlightColor}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
						</div>
					),
				},

				{
					lable: "highlightCompleteColor",
					item: (
						<div>
							{" "}
							<Input
								name='highlightCompleteColor'
								type='color'
								value={
									hanziComponentOptions.highlightCompleteColor
								}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
						</div>
					),
				},

				{
					lable: "drawingWidth",
					item: (
						<div>
							{" "}
							<Input
								name='drawingWidth'
								type='number'
								min={4}
								max={32}
								defaultValue={
									hanziComponentOptions.drawingWidth
								}
								className={styles["set-input"]}
								onChange={handleChangeSetting}
							/>
							<span style={{ marginLeft: 5 }}>px</span>
						</div>
					),
				},
			],
		},
	};

	const handleFormData = (formValue) => {
		if (!formValue) {
			setData([]);
			return;
		}
		let ArrayOfChineseCharacter = formValue
			.trim()
			.split(/\s+/)
			.filter((char) => /[\u4e00-\u9fa5]/g.test(char));
		setData(ArrayOfChineseCharacter);
	};

	const reading = async () => {
		setIsReading(true);
		let nodes = document.querySelector("#App-view-id").childNodes;
		function readAsyncSequence(Index, character) {
			const utterThis = new SpeechSynthesisUtterance(character);
			utterThis.lang = "zh-CN";
			speechSynthesis.speak(utterThis);

			// scroll đến element đang focus .
			nodes[Index].scrollIntoView({
				behavior: "smooth",
				block: "center",
			});

			nodes[Index].style.borderColor = "var(--LimeGreen)";
			return new Promise((resolve) => {
				utterThis.onend = function () {
					resolve(true);
				};
			});
		}
		const readAsync = () => {
			return new Promise(async (resolve) => {
				for (let i = 0; i < nodes.length; i++) {
					await readAsyncSequence(i, nodes[i].dataset.character).then(
						() => {
							nodes[i].style.borderColor = "transparent";
						}
					);
				}
				resolve(true);
			});
		};

		// ok!
		readAsync().then(() => {
			setIsReading(false);
		});
	};

	return (
		<div className={styles["App-container"]}>
			<Header>
				<Tippy
					interactive
					hideOnClick={false}
					delay={[0, 500]}
					render={(attrs) => (
						<div className='tippy-wrapper' tabIndex='-1' {...attrs}>
							<SettingMenu data={dataSettings} />
						</div>
					)}
				>
					<div className={styles["Header-item"]}>
						<FontAwesomeIcon icon={faGear} spin />
						<span>Customs</span>
					</div>
				</Tippy>
			</Header>
			<div className={styles["App-main"]}>
				<FormData
					onChange={handleFormData}
					onReading={{ reading, isReading }}
				/>
				<div className={styles["App-content"]}>
					<div id='App-view-id' className={styles["App-view"]}>
						{data.map((item, index) => {
							return (
								<HanziComponent
									key={index}
									character={item}
									options={hanziComponentOptions}
								/>
							);
						})}
					</div>
				</div>
			</div>
		</div>
	);
}

export default App;
