import styles from "./SetTingMenu.module.scss";

function SettingMenu({ data }) {
	return (
		<ul className={styles["menu-wrapper"]}>
			{data.data.listItems.map((item, index) => {
				return (
					<li
						key={index}
						onClick={(e) => {
							e.stopPropagation();
						}}
						className={styles["menu-item"]}
					>
						<span>{item.lable}</span>
						{item.item}
					</li>
				);
			})}
			<li
				style={{
					textAlign: "center",
					fontSize: "0.75rem",
					fontStyle: "italic",
				}}
			>
				Coding by{" "}
				<a
					target='_blank'
					rel='noreferrer'
					href='https://www.facebook.com/luongtuan.dev/'
					style={{ color: "inherit", fontWeight: "bold" }}
				>
					Luong van tuan
				</a>
			</li>
		</ul>
	);
}

export default SettingMenu;
