import { useState, memo, useRef } from "react";
import styles from "./FormData.module.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faXmark,
	faPaperPlane,
	faVolumeHigh,
} from "@fortawesome/free-solid-svg-icons";

function FormData({ onChange, onReading }) {
	const textAreaRef = useRef();
	const [value, setValue] = useState("");
	const handleChange = (e) => {
		setValue(e.target.value);
	};
	return (
		<div className={styles.wrapper}>
			<form
				onReset={(e) => {
					e.preventDefault();
					textAreaRef.current.focus();
					onChange(undefined);
					setValue("");
				}}
				onSubmit={(e) => {
					e.preventDefault();

					//const isHan = (c) => /^[\u3006\u3007\u4e00-\u9fff\u3400-\u4dbf]|[\ud840-\ud868\ud86a-\ud879\ud880-\ud883][\udc00-\udfff]|\ud869[\udc00-\udedf\udf00-\udfff]|\ud87a[\udc00-\udfef]|\ud884[\udc00-\udf4f]$/.test(c);
					/** Regex equivalent to \p{Han} in other programming languages. */
					// const HAN_REGEX = /[\u2E80-\u2E99\u2E9B-\u2EF3\u2F00-\u2FD5\u3005\u3007\u3021-\u3029\u3038-\u303B\u3400-\u4DB5\u4E00-\u9FD5\uF900-\uFA6D\uFA70-\uFAD9]/;

					//  cắt khoảng trắng đầu và cuối của string => tách thành mảng dựa vào khoảng trắng (regex) => lọc hán tự trong mảng.
					// let ArrayOfChineseCharacter = value
					// 	.trim()
					// 	.split(/\s+/)
					// 	.filter((char) => /[\u4e00-\u9fa5]/g.test(char));
					onChange([...value].join(" "));
				}}
			>
				<textarea
					ref={textAreaRef}
					className={styles["text-area"]}
					spellCheck={false}
					value={value}
					placeholder='...'
					onChange={handleChange}
					onPaste={(e) => e.preventDefault()}
					tabIndex={1}
				></textarea>
				<div className={styles["form-btn-group"]}>
					<button
						type='submit'
						tabIndex={2}
						disabled={!(value.trim().length > 0)}
					>
						<FontAwesomeIcon icon={faPaperPlane} />
					</button>

					<button
						type='reset'
						tabIndex={3}
						disabled={!(value.trim().length > 0)}
					>
						<FontAwesomeIcon icon={faXmark} />
					</button>
					<button
						onClick={onReading.reading}
						disabled={onReading.isReading}
					>
						<FontAwesomeIcon icon={faVolumeHigh} />
					</button>
				</div>
			</form>
		</div>
	);
}

export default memo(FormData);
