import { useEffect, useRef, useState, memo } from "react";
import HanziWriter from "hanzi-writer";
import styles from "./HanziComponentStyle.module.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faCirclePlay,
	faCirclePause,
	faEarListen,
	faPencil,
} from "@fortawesome/free-solid-svg-icons";

import Loading from "../Loading";
function HanziComponent({ character, options }) {
	const [isLoading, setIsLoading] = useState(false);
	const [mode, setMode] = useState("animate");
	const [isAnimation, setIsAnimation] = useState(false);
	const [isPlaying, setIsplaying] = useState(false);
	const RenderElement = useRef();
	const [Elem, setElem] = useState(null);
	const r = useRef();
	const utterThis = new SpeechSynthesisUtterance(character);
	utterThis.lang = "zh-CN";

	const iconColor = isAnimation ? "var(--LimeGreen)" : "inherit";
	let title =
		mode === "quiz"
			? "draw mode!"
			: isAnimation
			? "click to pause"
			: "click to animate";

	const changeMode = () => {
		setMode((prevMode) => {
			let m = prevMode === "animate" ? "quiz" : "animate";
			return m;
		});
	};
	useEffect(() => {
		setIsLoading(true);
		let node = RenderElement.current;
		let el = HanziWriter.create(node, character, {
			...options,
			onLoadCharDataSuccess: function (data) {
				// delay :v
				let tID = setTimeout(() => {
					clearTimeout(tID);
					setIsLoading(false);
				}, 500);
			},
			onLoadCharDataError: function (error) {
				// thong bao loi ra ui
				loadingRef.current.innerHTML = `Error: ${error}`;
			},
		});
		setElem(el);
		return () => {
			node.innerHTML = null;
		};
	}, [character]);

	useEffect(() => {
		if (Elem) {
			Elem.quiz({
				onMistake: function (strokeData) {
					r.current.innerText = "wrong!";
				},
				onCorrectStroke: function (strokeData) {
					r.current.innerText = "correct!";
				},
				onComplete: function (summaryData) {
					let tID = setTimeout(() => {
						clearTimeout(tID);
						changeMode();
					}, 1000);
				},
			});
		}
	}, [mode]);

	const changeAnimationState = (e) => {
		if (!isPlaying && mode !== "quiz") {
			setIsplaying(true);
			setIsAnimation(true);
			Elem.animateCharacter({
				onComplete: function () {
					setIsplaying(false);
					setIsAnimation(false);
				},
			});
		}

		if (isAnimation && isPlaying && mode !== "quiz") {
			Elem.pauseAnimation();
		} else {
			Elem.resumeAnimation();
		}
		if (mode !== "quiz") setIsAnimation(!isAnimation);
	};
	const loadingRef = useRef();
	return (
		<div className={styles["wrapper"]} data-character={character}>
			{isLoading && <Loading ref={loadingRef} />}
			{mode === "quiz" && (
				<div className={styles["quiz-info"]}>
					<div ref={r}></div>
				</div>
			)}
			<div
				className={styles["Element-Diplay-Hanzi"]}
				style={{ visibility: `${isLoading ? "hidden" : "visible"}` }}
				ref={RenderElement}
				onClick={changeAnimationState}
				title={title}
			></div>

			<div className={styles["controler"]}>
				<button
					className={styles["btn"]}
					onClick={changeAnimationState}
					style={{
						color: iconColor,
					}}
					title={title}
				>
					{!isAnimation ? (
						<FontAwesomeIcon icon={faCirclePlay} />
					) : (
						<FontAwesomeIcon icon={faCirclePause} />
					)}
				</button>
				<div className={styles["right-section"]}>
					<button
						className={styles["btn"]}
						onClick={(e) => {
							e.target.style.color = "var(--LimeGreen)";
							speechSynthesis.speak(utterThis);
							utterThis.onend = function () {
								this.style.color = "inherit";
							}.bind(e.target);
						}}
					>
						<FontAwesomeIcon icon={faEarListen} />
					</button>
					<button
						className={styles["btn"]}
						onClick={changeMode}
						style={{
							color: `${
								mode === "animate"
									? "inherit"
									: "var(--LimeGreen)"
							}`,
						}}
					>
						<FontAwesomeIcon icon={faPencil} />
					</button>
				</div>
			</div>
		</div>
	);
}

export default memo(HanziComponent);
